<script>
	import { createEventDispatcher, onMount } from "svelte";
    import Job from "./Job.svelte";
    import { EXPERIENCE } from "./data";
	import { fade } from "svelte/transition";
    
    export let experience = [];
    export let selected;

    let showCurrent = false;

    const dispatch = createEventDispatcher();


    const click = (job, e) => {
        e?.preventDefault();

        if(selected?.title == job?.title) {
            return dispatch('reset');
        }

        dispatch('click', job);
    };


    onMount(() => {
        setTimeout(() => {
            showCurrent = true;
        }, 1000);
    });


</script>

<div class="experience">
    <h4>- experience</h4>

    {#each experience as job}
        <div class="job" 
            class:selected={job.title == selected?.title} 
            class:current={showCurrent && job.current}
            class:mark-date={job.title == selected?.title || (showCurrent && !selected && job.current)}
            on:click={(e) => click(job, e)}>
            <div class="job-title">
                <div class="icon">
                    {#if selected?.title === job.title}
                        <span class="material-symbols-outlined">
                            radio_button_checked
                        </span>
                    {:else}
                        <span class="material-symbols-outlined">
                            radio_button_unchecked
                        </span>
                    {/if}
                </div>
                <div class="titl">
                    <h3>
                        {job.title} 
                    </h3>
                    <div class="date">
                        {job.short_date_start}

                        {#if job.short_date_end}
                        ~ {job.short_date_end}
                        {/if}
                    </div>
                </div>
            </div>
        </div>
    {/each}


