<script>
    import { SKILLS } from "./data";
	import { fade, slide } from "svelte/transition";

    let skills = SKILLS.map(s => {
        return {
            ...s,
            open: false
        }
    });

    const open = (e, skill) => {
        e.preventDefault();
        
        skills = skills.map(s => {
            if(s.title === skill.title) {
                s.open = !skill.open;
            }
            return s;
        });
    };

    let expanded = false;

    const toggleSkills = () => {
        expanded = !expanded;
        skills = skills.map(s => {
            return {
                ...s,
                open: expanded
            }
        });
    };



    const onKeyDown = (e) => {
        switch(e.keyCode) {
            case 13:
                toggleSkills();
                break;
        }
    }
</script>



<svelte:window on:keydown|preventDefault={onKeyDown} />


<div class="skillset">   
    <h4 class="skillset-btn">
        <a href="#" on:click={toggleSkills}>
            {#if expanded }
                <span>-</span>
            {:else }
                <span>+</span>
            {/if}
            skillset
        </a>
    </h4>

    <div class="list">
        {#each skills as skill}
        <div class="skill" class:open={skill.open}>
            <div class="skill-title" >
                <h3>
                    <a href="" on:click={(e) => open(e, skill)}>
                        <span class="material-symbols-outlined">
                            {skill.icon}
                        </span>
                        {skill.title}
                    </a>
                </h3>
            </div>


            {#if skill.open} 
                <div class="skill-desc" in:slide={{ delay: 0, duration: 300 }} out:slide={{ delay: 10, duration: 400 }} >
                    <ul in:fade={{ duration: 340 }} out:fade={{ duration: 350 }}>
                        {#each skill.items as item}
                            <li>{item.title}</li>
                        {/each }
                    </ul>
                </div>
            {/if }
        </div>
        {/each }
    </div>
</div>




