<script>
	import { createEventDispatcher } from "svelte";

    const onKeyDown = (e) => {
        e.stopPropagation();
        switch(e.keyCode) {
            case 27: // escape
                close()
                break;
        }
    };

    const dispatch = createEventDispatcher();
    const close = () => {
        dispatch('close');
    }

</script>

<div class="modal">
    <div class="modal-body">
        <slot />
    </div>
</div>



<svelte:window on:keydown|preventDefault={onKeyDown} />


<style lang="scss">
    .modal {
        position: fixed;
        left: 0;
        top: 10px;
        width: 100%;
        height: 100%;;
        backdrop-filter: blur(10px);
        z-index: 999;
    }

    .modal-body {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        padding: 0px 35px;
    }
</style>