<script>
    import Presentation from "./lib/Presentation.svelte";
    import Experience from "./lib/Experience.svelte";
	import Skillset from "./lib/Skillset.svelte";
	import { onMount } from "svelte";
	import { fade, fly, slide } from "svelte/transition";
	import Modal from "./lib/Modal.svelte";
	import Job from "./lib/Job.svelte";
    import { EXPERIENCE } from "./lib/data";

    let showModal = false;
    let job;
    let sliderItem = {
        src: undefined
    };
    let totalItems = 0;

    let showExperience = false;
    let showSkillset = false;

    let fireflies = new Array(20).fill(1);

    

    const setImg = (src, index) => {
        sliderItem = {
            src: undefined,
            index
        }

        setTimeout(() => {
            sliderItem.src = src;
        }, 10)
        
    };

    const nextSlide = () => {
        if(!sliderItem?.src) {
            return;
        }

        const nextSlideId = sliderItem.index + 1;
        const nexSlide = job.attachments[nextSlideId];
        
        if(nexSlide) {
            sliderItem = {
                ...nexSlide,
                index: nextSlideId
            }
        }
    };

    const prevSlide = () => {
        if(!sliderItem?.src) {
            return;
        }

        const nextSlideId = sliderItem.index - 1;
        const nexSlide = job.attachments[nextSlideId];
        
        if(nexSlide) {
            sliderItem = {
                ...nexSlide,
                index: nextSlideId
            }
        }
    };

    const setGallery = (evt) => {
        const attachment = evt.detail;
        showModal = true;

        if(job?.attachments.length) {
            totalItems = job.attachments.length;
        }

        (job.attachments || []).map((a, i) => {
            if(a.src === attachment.src) {
                setImg(a.src, i);
            }
            return;
        })

    };

    const closeModal = () => {
        showModal = false;
    }


    const closeGalleryOrRemoveJob = () => {
        if(showModal) {
            return closeModal();
        }
        
        removeJob();
    }


    // JOB DETAILS

    let lastJobSelected;
    let experience = EXPERIENCE;
    const setJob = (j) => {
        job = j ? { ...j } : undefined;
    };

    const selectJob = (evt) => {
        setJob(evt.detail);
    };

    const removeJob = () => {
        if(job) {
            lastJobSelected = { ...job };
        }
        
        setJob();
    };

    const nextJob = () => {
        let nextJob = experience[0];

        if(job) {
            const key = experience.findIndex(j => j.title == job.title) + 1;
            
            if(experience[key]) {
                nextJob = experience[key]
            }
        }
    
        if(nextJob) {
            setJob(nextJob);
        }
    }

    const prevJob = () => {
        let nextJob = experience[experience.length - 1];

        if(job) {
            const key = experience.findIndex(j => j.title == job.title) - 1;
            
            if(experience[key]) {
                nextJob = experience[key]
            }
        }

    
        if(nextJob) {
            setJob(nextJob);
        }
    }

    const lastJob = () => {
        if(lastJobSelected) {
            setJob(lastJobSelected);
        }
    }

    const space = () => {
        if(job) {
            removeJob();
        } else if(lastJobSelected) {
            setJob(lastJobSelected);
        }
    }

    
    const onKeyDown = (e) => {
        switch(e.keyCode) {
            case 38:
                prevJob();
                break;
            case 40:
                nextJob();
                break;
            case 37:
            case 27:
            case 13:
                closeGalleryOrRemoveJob()
                break;
            case 39:
                lastJob()
                break;
            case 32:
                space();
                break;
        }
    }











    onMount(() => {
        setTimeout(() => {
            showExperience = true;
        }, 5000);

        setTimeout(() => {
            showSkillset = true;
        }, 6000);
    })
</script>

<svelte:window on:keydown|preventDefault={onKeyDown} />


<div class="main">
    <Presentation />

    <div class="columns">
        {#if showExperience }
            <div class="col exp" class:job-selected-exp={job} transition:fade={{ delay: 400, duration: 600 }}>
                <Experience 
                    experience={experience} 
                    selected={job} 
                    
                    on:click={selectJob} 
                    on:reset={removeJob} 
                />
            </div>
        {/if }


        {#if showSkillset }
            <div class="col" class:job-selected={job} transition:fade={{ delay: 630, duration: 1200 }}>
                <Skillset />
            </div>
        {/if }
    </div>



    
</div>


{#if job}

    <div class="job"  in:slide={{ duration: 450 }} out:slide={{ duration: 500 }}>
        <a href="#" out:fade={{ duration: 300 }} in:fade={{ duration: 350 }} class="close-job" on:click={removeJob} >
            <span class="material-symbols-outlined">
                <!-- close -->
                keyboard_double_arrow_down
            </span>
        </a>

        <Job job={job} on:gallery={setGallery}  on:close={removeJob} />
    </div>

    
    {#if showModal}
        <div in:fade={{ duration: 50 }} out:fade={{ duration: 20 }}>
            <Modal on:close={closeModal}>
                <div class="slider">
                    <div class="slider-title">
                        <h2>{job.title}</h2>

                        <div class="slider-ctrls">
                            <ul>
                                <li>
                                    <a href="#" on:click={prevSlide}>
                                        <span class="material-symbols-outlined">
                                            line_start_arrow
                                        </span>
                                    </a>
                                </li>

                                <li>
                                    <span>
                                        {sliderItem.index + 1}/{totalItems}
                                    </span>
                                </li>

                                <li>
                                    <a href="#" on:click={nextSlide}>
                                        <span class="material-symbols-outlined">
                                            line_end_arrow
                                        </span>
                                    </a>
                                </li>

                                

                                <li>
                                    <a href="#" on:click={closeModal}>
                                        <span class="material-symbols-outlined">
                                            close
                                        </span>
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>


                    <div class="slide-img">
                        {#key sliderItem?.src}
                            <div in:fade={{ duration: 130 }} out:fade={{  duration: 80 }} on:introstart on:outroend>
                                <img src={sliderItem?.src}>
                            </div>
                        {/key}
                    </div>

                    <!-- {#each job.attachments as attachment }
                        <img src={attachment.src}>
                    {/each } -->
                </div>
            </Modal>
        </div>
    {/if }

{/if }



<div class="bg">
{#each fireflies as f, index}
    <div class="firefly">
        <!-- {#if index %2 == 0} -->
        <span class="material-symbols-outlined">
            code
        </span>
        <!-- {:else }

        <span class="material-symbols-outlined">
            bug_report
            </span>
        {/if } -->
    </div>
{/each}
</div>


