        // -o-transition: all .5s ease-in;
        // transition: all .5s ease-in;
        // justify-content: center;
        // margin-right: 150px;
    }

 

    .slider {
        max-width: 1024px;
        min-width: 500px;
        width: 100%;

        

        img {
            width: 100%;
            border-radius: 5px;
        }

        .slide-img {
            overflow: hidden;
            min-width: 500px;
            max-width: 1024px;
            width: 100%;
            // height: 720px;
        }


        .slider-title {
            display: flex;
            align-items: center;
            justify-content: space-between;


            h2 {
                color: #7f8fa6;
            }
        

            .slider-ctrls {
                ul {
                    list-style: none;
                    padding: 0;
                    margin: 0;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    width: 100%;

                    li {
                        color: #487eb0;

                        a {
                            color: #487eb0;
                            display: block;
                        }
                    }
                }
            }


        }

    }

    

    $quantity: 20;

    .firefly {
        position: fixed;
        left: 50%;
        top: 50%;
        width: 0.4vw;
        height: 0.4vw;
        margin: -0.2vw 0 0 9.8vw;
        animation: ease 200s alternate infinite;
        pointer-events: none;
        color: #2f3640;

        &::before,
        &::after{
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            transform-origin: -10vw;
        }
        
        &::before {
            background: #12181d;
            opacity: 0.4;
            animation: drift ease alternate infinite;
        }
        
        &::after {
            background: #2f3640;
            opacity: 0;
            box-shadow: 0 0 0vw 0vw #2f3640;
            animation: drift ease alternate infinite, flash ease infinite;
        }
    }
  
    // Randomize Fireflies Motion
    @for $i from 1 through $quantity {
        $steps: random(12) + 14;
        $rotationSpeed: random(10) + 8s;
        
        .firefly:nth-child(#{$i}) {
            animation-name: move#{$i};

            &::before {
                animation-duration: #{$rotationSpeed};
            }

            &::after {
                animation-duration: #{$rotationSpeed}, random(6000) + 6000ms;
                animation-delay: 0ms, random(8000) + 800ms;
            }
        }

        @keyframes move#{$i} {
            @for $step from 0 through $steps {
                $value: calc(100 / $steps);
                #{$step * $value + "%"} {
                    transform: translateX(random(100) - 50vw) translateY(random(100) - 50vh) scale(calc(random(75) / 100 + .25));
                }
            }
        }

        @keyframes drift {
            0% {
                transform: rotate(0deg);
            }
            100%{
                transform: rotate(360deg);
            }
        }

        @keyframes flash {
            0%, 30%, 100% {
                opacity: 0;
                box-shadow: 0 0 0vw 0vw #1e272e;
            }
            5% {
                opacity: 1;
                box-shadow: 0 0 2vw 0.4vw #1e272e;
            }

        }
    }
</style>